/******************
    Variable css
********************/
:root {
  --theme-color: 23, 28, 38;
  --title-color: 23, 28, 38;
  --content-color: 127, 131, 132;
  --box-bg: 243, 244, 246;
  --line-color: 232, 232, 233;
  --secondary-color: 255, 180, 0;
  --success-color: 32, 177, 73;
  --error-color: 255, 75, 75;
  --accent-color: 58, 109, 229;
  --white: 255, 255, 255;
  --black: 0, 0, 0;
}

/************************ 
    Reset css 
***********************/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

section,
.section-t-space {
  padding-top: calc(30px + 70 * (100vw - 320px) / 1600);
}

.section-b-space {
  padding-bottom: calc(30px + 70 * (100vw - 320px) / 1600);
}

.custom-container {
  padding: 0 calc(15px + 205 * (100vw - 320px) / 1600);
}

.custom-container-lg {
  padding: 0 calc(15px + 50 * (100vw - 320px) / 1600);
}

.theme-color {
  color: rgba(var(--theme-color), 1) !important;
}

.theme-bg {
  background-color: rgba(var(--theme-color), 1) !important;
}

.box-background {
  background-color: rgba(var(--box-bg), 1) !important;
}

.--theme-color {
  color: rgba(var(--theme-color), 1) !important;
}

/************************ 
    Typography css 
***********************/
body {
  position: relative;
  font-family: "Outfit", sans-serif;
  background-blend-mode: screen;
  background: rgba(var(--white), 1);
}

h1 {
  font-size: calc(22px + 28 * (100vw - 320px) / 1600);
  line-height: 1;
  margin-bottom: 0;
}

h2 {
  font-size: calc(24px + 8 * (100vw - 320px) / 1600);
  line-height: 1.2;
  margin-bottom: 0;
  display: inline-block;
}

h3 {
  font-size: calc(18px + 8 * (100vw - 320px) / 1600);
  line-height: 1;
  margin-bottom: 0;
}

h4 {
  font-size: calc(16px + 6 * (100vw - 320px) / 1600);
  line-height: 1;
  margin-bottom: 0;
}

h5 {
  font-size: calc(15px + 8 * (100vw - 320px) / 1600);
  margin-bottom: 0;
}

h6 {
  font-size: calc(14px + 8 * (100vw - 320px) / 1600);
  margin-bottom: 0;
}

p {
  font-size: calc(12px + 8 * (100vw - 320px) / 1600);
  margin-bottom: 0;
}

a {
  text-decoration: none;
}

ul {
  padding-left: 0;
  margin-bottom: 0;
}

li {
  list-style: none;
  display: inline-block;
  font-size: 14px;
}

/*********************
    Button css 
**********************/
.btn {
  padding: calc(8px + 2 * (100vw - 320px) / 1600) calc(15px + 5 * (100vw - 320px) / 1600);
  display: flex;
  align-items: center;
  gap: 5px;
  font-weight: 500;
  font-size: calc(12px + 6 * (100vw - 320px) / 1600);
  border-radius: 6px;
  white-space: nowrap;
}
.btn.outline-btn {
  color: rgba(var(--theme-color), 1);
  background-color: transparent;
  border: 1px solid rgba(var(--theme-color), 1);
  transition: all 0.5s ease-in-out;
}
.btn.outline-btn:hover {
  background-color: rgba(var(--theme-color), 1) !important;
  color: rgba(var(--white), 1) !important;
}
.btn.theme-btn {
  background-color: rgba(var(--theme-color), 1);
  color: rgba(var(--white), 1);
}
.btn.theme-btn:hover {
  background-color: rgba(var(--theme-color), 1) !important;
  color: rgba(var(--white), 1) !important;
}
.btn.theme-btn:active {
  background-color: rgba(var(--theme-color), 1) !important;
  color: rgba(var(--white), 1) !important;
  border-color: rgba(var(--theme-color), 1);
}
.btn.purchase-btn {
  color: rgba(var(--theme-color), 1);
  background-color: rgb(236, 236, 236);
  border-radius: 6px;
  transition: all 0.5s ease-in-out;
}
.btn.purchase-btn:hover {
  background-color: rgba(var(--theme-color), 1) !important;
  color: rgba(var(--white), 1) !important;
}
.btn.purchase-btn:active {
  background-color: rgba(var(--theme-color), 1) !important;
  color: rgba(var(--white), 1) !important;
  border-color: rgba(var(--theme-color), 1);
}

/*========================
    animation CSS start
==========================*/
@keyframes mover {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-20px);
  }
}
/*========================
    tap to top CSS start
==========================*/
.scroll-to-top {
  z-index: 4;
  position: fixed;
  right: 20px;
  bottom: 30px;
  width: calc(40px + 10 * (100vw - 320px) / 1600);
  height: calc(40px + 10 * (100vw - 320px) / 1600);
  border-radius: 10px;
  background-color: rgba(var(--theme-color), 1);
  border: none;
  font-size: calc(20px + 10 * (100vw - 320px) / 1600);
  transition: all 0.4s ease;
  outline: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.scroll-to-top .arrow {
  --Iconsax-Color: rgba(255, 255, 255, 1);
}

/***************************
    Title css 
****************************/
.title {
  margin-bottom: calc(20px + 20 * (100vw - 320px) / 1600);
}
.title h2 {
  font-size: calc(24px + 26 * (100vw - 320px) / 1600);
  color: rgba(var(--title-color), 1);
}
.title h3 {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: calc(24px + 12 * (100vw - 320px) / 1600);
  color: rgba(var(--title-color), 1);
}
.title p {
  width: 60%;
  font-weight: 400;
  line-height: 1.5;
  font-size: calc(14px + 6 * (100vw - 320px) / 1600);
  margin-left: auto;
  margin-right: auto;
  color: rgba(var(--content-color), 1);
}
@media (max-width: 991px) {
  .title p {
    width: 100%;
  }
}

/************************
    header section 
 ************************/
header {
  position: absolute;
  width: 100%;
  padding-block: calc(10px + 10 * (100vw - 320px) / 1600);
  z-index: 2;
}
header.sticky {
  position: sticky;
  top: 0;
  left: 0;
  background-color: rgba(var(--white), 1);
  border: none;
}
header.sticky::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: -1;
  background-color: rgba(var(--white), 1);
  box-shadow: 0 8px 10px rgba(var(--black), 0.05);
  transition: all 0.3s ease;
}
header.sticky .navbar .navbar-toggler .navbar-toggler-icon .icon {
  --Iconsax-Color: rgba(var(--theme-color), 1);
  --Iconsax-Size: 20px;
}
header.sticky .navbar-nav .nav-item .nav-link.active {
  color: rgba(var(--theme-color), 1);
}
header.sticky .navbar-nav .nav-item .nav-link:active {
  color: rgba(var(--theme-color), 1);
}
header.sticky .navbar-nav .nav-item .nav-link:hover {
  color: rgba(var(--theme-color), 1);
}

.navbar {
  justify-content: unset;
  align-items: center;
}
.navbar .btn {
  margin-left: auto;
}
.navbar .logo {
  height: calc(25px + 15 * (100vw - 320px) / 1600);
}
.navbar .navbar-toggler {
  padding: 0;
  border: 0;
}
.navbar .navbar-toggler .navbar-toggler-icon {
  width: auto;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: calc(10px + 10 * (100vw - 320px) / 1600);
  color: rgba(var(--theme-color), 1);
  border-radius: 6px;
  background-image: none;
}
.navbar .navbar-toggler .navbar-toggler-icon .icon {
  --Iconsax-Color: rgba(var(--theme-color), 1);
}
.navbar .navbar-toggler:focus {
  box-shadow: none;
}
.navbar .navbar-collapse .navbar-nav {
  gap: calc(10px + 20 * (100vw - 320px) / 1600);
}
.navbar .navbar-collapse .navbar-nav .nav-item .nav-link.active {
  color: rgba(var(--theme-color), 1);
}
@media (max-width: 1200px) {
  .navbar .navbar-collapse {
    background-color: rgba(var(--theme-color), 1);
    color: rgba(var(--content-color), 1);
    padding: 15px;
    margin-top: 8px;
    border-radius: 8px;
  }
  .navbar .navbar-collapse .navbar-nav {
    gap: 10px;
  }
  .navbar .navbar-collapse .navbar-nav .nav-item .nav-link.active {
    color: rgba(var(--white), 1) !important;
  }
  .navbar .navbar-collapse .navbar-nav .nav-item .nav-link:hover {
    color: rgba(var(--white), 1) !important;
  }
}
.navbar .navbar-nav {
  display: flex;
  gap: calc(10px + 20 * (100vw - 320px) / 1600);
}
.navbar .navbar-nav .nav-item .nav-link {
  text-transform: capitalize;
  color: rgba(var(--content-color), 1);
  font-weight: 500;
  font-size: calc(14px + 4 * (100vw - 320px) / 1600);
  padding: 0;
}
.navbar .navbar-nav .nav-item.active {
  color: rgba(var(--theme-color), 1);
}

/***********************
    home section 
************************/
.home-wrapper {
  background-image: url(../images/background/home-bg.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  width: 100%;
  height: 100vh;
}
@media (max-width: 1200px) {
  .home-wrapper {
    padding-top: calc(60px + 40 * (100vw - 320px) / 880);
    height: auto;
    padding-bottom: 30px;
  }
}
.home-wrapper .home-content {
  display: flex;
  align-items: center;
  height: 100%;
}
.home-wrapper .home-content h1 {
  width: 100%;
  font-weight: 600;
  line-height: 1.3;
  text-transform: capitalize;
  color: rgba(var(--theme-color), 1);
}
.home-wrapper .home-content h1 span {
  color: rgba(var(--secondary-color));
}
.home-wrapper .home-content h5 {
  line-height: 1.2;
  font-weight: 400;
  margin-bottom: calc(5px + 5 * (100vw - 320px) / 1600);
  color: rgba(var(--title-color), 1);
  padding: 6px calc(15px + 10 * (100vw - 320px) / 1600);
  border: 2px solid rgba(var(--theme-color), 1);
  display: inline-block;
  border-radius: 30px;
}
.home-wrapper .home-content .button-part {
  margin-top: calc(20px + 20 * (100vw - 320px) / 1600);
  display: flex;
  align-items: center;
  gap: calc(10px + 10 * (100vw - 320px) / 1600);
}
.home-wrapper .home-content p {
  margin-top: 5px;
  line-height: 1.5;
  color: rgba(var(--content-color), 1);
}
.home-wrapper .home-img {
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
}
@media (max-width: 1199px) {
  .home-wrapper .home-img {
    display: none;
  }
}
.home-wrapper .home-img .phone {
  position: relative;
  overflow: hidden;
  border-radius: 45px;
}
.home-wrapper .home-img .phone iframe {
  position: relative;
  border-radius: 45px;
  width: 320px;
  overflow: hidden;
  height: 650px;
  border: 5px solid black;
}
@media (max-width: 1300px) {
  .home-wrapper .home-img .phone iframe {
    width: 320px;
    height: 580px;
  }
}
.home-wrapper .car1 {
  position: absolute;
  top: 140px;
  left: 36%;
  width: 40px;
}
@media (max-width: 1200px) {
  .home-wrapper .car1 {
    display: none;
  }
}

/***********************
    app section 
************************/
.app-banner {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 425px) {
  .app-banner {
    height: calc(90px + 30 * (100vw - 320px) / 1600);
  }
}

/***********************
    application section 
************************/
.application-wrapper {
  position: relative;
}
.application-wrapper .application-img {
  width: calc(250px + 370 * (100vw - 320px) / 1600);
  display: flex;
  align-items: center;
  justify-content: center;
}
.application-wrapper .application-img .app-img {
  width: 100%;
}
.application-wrapper .application-content h2 {
  font-weight: 600;
  line-height: 1.3;
  color: rgba(var(--theme-color), 1);
  margin-bottom: 10px;
}
.application-wrapper .application-content p {
  line-height: 1.5;
  font-weight: 400;
  font-size: calc(14px + 8 * (100vw - 320px) / 1600);
  margin-bottom: calc(5px + 5 * (100vw - 320px) / 1600);
  color: rgba(var(--content-color), 1);
}
.application-wrapper .effect1 {
  position: absolute;
  top: -50%;
  left: -210px;
  transform: rotate(180deg) translateY(-50%);
  opacity: 0.4;
}
@media (max-width: 768px) {
  .application-wrapper .effect1 {
    display: none;
  }
}
.application-wrapper .effect2 {
  position: absolute;
  top: 50%;
  right: -210px;
  transform: translateY(-50%);
  opacity: 0.4;
}
@media (max-width: 768px) {
  .application-wrapper .effect2 {
    display: none;
  }
}

/***************************
    Feature css
****************************/
.features-wrapper {
  background-color: rgba(var(--theme-color), 1);
}
@media (max-width: 576px) {
  .features-wrapper {
    margin-top: calc(0px + -45 * (100vw - 320px) / 1600);
  }
}
.features-wrapper .card-body {
  height: 100%;
