}
@media (max-width: 768px) {
  .screenshots-section .effect4 {
    display: none;
  }
}

/***************************
   app-features css
****************************/
.inner-page-wrapper {
  background-color: rgba(var(--box-bg), 1);
}
.inner-page-wrapper .inner-page-img {
  margin-top: calc(20px + 50 * (100vw - 320px) / 1600);
  padding-inline: calc(20px + 40 * (100vw - 320px) / 1600);
}

/***************************
   app-features css
****************************/
.app-features-sec {
